import React, { useState } from "react";
import { ArrowsAngleExpand } from 'react-bootstrap-icons';
import "./sidebar-styles.css";
import { Cell, Column, Row, TableView, TableBody, TableHeader, View } from '@adobe/react-spectrum'
import { defaultTheme } from '@adobe/react-spectrum';

function SidebarTable({ documentTable, updatePdfRendered }) {
  const [isOpen, setIsOpen] = useState(false);

  const handleTrigger = () => setIsOpen(!isOpen);

  // console.log("in side bar");
  // console.log(documentTable[pdfRendered].highlightsArr[0].creator.isReviewed);

  return (

    <div className={`sidebar ${isOpen ? "sidebar--open" : ""}`}>
      <div className="trigger" onClick={handleTrigger}>
        <ArrowsAngleExpand />
      </div>

      <TableView
        colorScheme="lightest"
        aria-label="Example table with static contents"
        selectionMode="single"
        disallowEmptySelection
        selectionStyle="highlight"
        // onAction={(key) => console.log(`clicked item ${key}...`)}
        onAction={(key) => updatePdfRendered(key)}
        isQuiet
        // density="compact"
        overflowMode="wrap"
      >
        <TableHeader>
          <Column minWidth={120}>Name</Column>
          <Column align="end">Impact</Column>
          <Column maxWidth={120}>Reviewed?</Column>
          <Column maxWidth={80}>TFC</Column>
          <Column align="end">Revenue</Column>
        </TableHeader>
        <TableBody>
          {Object.entries(documentTable).map(([key, value]) => {
            return (
              <Row key={key} className={`${documentTable[key].highlightsArr[0].creator.isReviewed === true ? "review-true" : "review-false"}`}>
                <Cell>{key}</Cell>
                <Cell>{`${value.impactFactor}`}</Cell>
                <Cell>{`${value.highlightsArr[0].creator.isReviewed === true ? "yes" : "no"}`}</Cell>
                <Cell>{`${value.highlightsArr[0].creator.isCorrect === true ? "yes" : "no"}`}</Cell>
                <Cell>{`${value.revenue}`}</Cell>
              </Row>
            );
          })}
        </TableBody>
      </TableView>

    </div> // end side bar div

  );
}

export default SidebarTable;
